<template>
  <div>
    <mrc_ess_table v-if="$route.name=='staffGroup'"
                   :headers='headers'
                   pageName='addStaffGroup'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['member']"
                   :items='desserts'>

      <template v-slot:top>
        <v-card-title class="cardtitle"
                      style="height:60px">
          <mrc_ess_searchInput :isShow='true'
                               style="position:absolute;right:10px;top:18px;"></mrc_ess_searchInput>
        </v-card-title>
      </template>

      <template v-slot:item.member='{item}'>
        {{item}}
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
    <mrc_ess_myDialog v-model="dialog"
                      :title="$t('label_Upload_CVS')"
                      width='847'
                      :disabled='false'>
      <mrc_ess_myStep :list='stepList'
                      v-model="stepIndex">
        <template v-slot:1>
          <span>
            文件
          </span>
          <mrc_ess_brandupload :isShow='false'></mrc_ess_brandupload>
          <mrc_ess_submitBtn @success='changeIndex(2)'
                             :successText="$t('label_The_next_step')"
                             @cancel='changeIndex(0)'></mrc_ess_submitBtn>
        </template>
        <template v-slot:2>

          <mrc_ess_submitBtn @success='changeIndex(3)'
                             :cancel="$t('label_The_previous_step')"
                             @cancel='changeIndex(1)'></mrc_ess_submitBtn>
        </template>
      </mrc_ess_myStep>
    </mrc_ess_myDialog>
  </div>
</template>

<script>
import {
  getStaffOfficialList
  , addStaffOfficial
  , updateStaffOfficial
  , deleteStaffOfficial
  , detailStaffOfficial
} from '@/service/api/staffmsg.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      stepIndex: 1,

      dialog: false,
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      desserts: [],
    };
  },
  computed: {
    stepList () {
      return [
        {
          name: this.$t('label_Importing_files')
        },
        {
          name: this.$t('label_result')
        },
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View') },
        { name: this.$t('label_Edit') },
        { name: this.$t('label_delete') },
      ]
    },
    headers () {
      return [
        { text: '群组名称', align: 'start', value: 'name', sortable: false },
        { text: '成员', value: 'member', sortable: false, align: 'center', },
        { text: '', value: 'actions', sortable: false, align: 'end', width: '100' },
      ]
    },
  },
  created () { },
  watch: {
    $route (v) {
      if (v.name == 'staffGroup') {
        this.getlist()
      }
    }
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  mounted () {
    this.getlist()
  },

  methods: {
    changeIndex (v) {
      if (v == 0) {
        this.dialog = false
      }
      else if (v == 3) { //提交代码逻辑

      }
      else {
        this.stepIndex = v
      }
    },
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getStaffOfficialList({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    addStaffOfficial () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'addStaffOfficial'
      })
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      // this.$store.commit('set_subPage_name', item.job_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailStaffOfficial'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          // this.$store.commit('set_subPage_name', item.job_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailStaffOfficial'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteStaffOfficial({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                if (this.desserts.length = 1) this.options.cpage--
                this.getlist()
              }
            })
          })
          break
      }
    },
    success () { },
    cancel () { }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
.dowanTemplate {
  position: absolute;
  right: 180px;
  top: -50px;
}
</style>
